.activity-cards-wrapper {
    min-height: 700px;
    max-height: 1000px;
    width: auto;
    margin: 5rem;
    text-align: center;
    
}

.activity-card-area {
    position: relative;
    display: inline;
}

.activity-card-container {
    display: inline-block;
    width: 40rem;
    height: 20rem;
    margin: 2rem;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 225, 0.2);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 225, 0.2);
    -o-transition: 0.3s;
    transition: 0.3s;
    background: whitesmoke;
    border-top-right-radius: 10%;
    border-top-left-radius: 2%;
    border-bottom-right-radius: 10%;
    border-bottom-left-radius: 2%;

    -webkit-transition: 0.3s;
}

.activity-card-container:hover {
    -webkit-box-shadow: 0 40px 80px 0 rgba(0, 0, 225, 0.3);
            box-shadow: 0 40px 80px 0 rgba(0, 0, 225, 0.3);
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

.event-substance {
    padding-top: 1rem;
    padding-bottom: 1rem;
}

.user-creator-info {
    margin: 0;
    padding: 0;
    background: gainsboro;
    height: -webkit-fill-available;
    height: -moz-available;
    height: stretch;
    border-radius: 10%;

    -webkit-transition: 0.3s;
}

.user-creator-info:hover {
    background: rgba(0, 0, 225, 0.3);
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

.event-activity {
    margin-bottom: 1rem;
    padding: 1rem;
    text-align: center;
    width: 50%;
    border-radius: 10%;
    -webkit-transition: 0.3s;
}

.event-activity:hover {
    -webkit-box-shadow: 5px 5px 10px 5px rgba(0, 0, 225, 0.3);
            box-shadow: 5px 5px 10px 5px rgba(0, 0, 225, 0.3);
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

.user-created {
    display: block;
    margin: 1rem auto 1rem auto;
    padding-top: 1rem;
    padding-bottom: 1rem;
    text-align: center;
}

.user-profile-picture {
    display: block;
    margin: 1rem auto;
    width: 7rem;
    height: 7rem;
}

.user-flake-score {
    margin: 3rem;
    text-align: center;
    display: block;
}

.event-info {
    height: 100%;
    width: 100%;
    text-align: left;
}
.card-text {
    height: 140px;
    overflow: hidden;
    padding: 1rem;
}

ul {
    margin-left: -22px;
}


